.ui.cards.gallery {
  margin-top: 1em;
}
.album.ellipsis.button {
  background: 0 0!important;
}
.ui.photos {
  display: flex;
  margin: 1em 0em;
  flex-flow: row wrap;
  justify-content: space-between;
}
.ui.photos .photo {
  position: relative;
  height: 225px;
  margin: 0.875em 0.1em;
  border-radius: .28571429rem;
}
.ui.photos .photo > .ui.dimmer {
  border-radius: inherit!important;
  background-color: rgba(0, 0, 0, 0.6);
}
.ui.photos .photo img {
  width: auto;
  height: 100%;
}
.ui.button.bottom.spacing {
  margin-bottom: 2em;
}
#noPhotosPlaceholder {
  margin-top: 3em;
}
